const url = {
  auth: {
    login: '/auth/signin',
    register: '/auth/signup',
    emailAuth: '/auth/email',
  },
  user: {
    login: '/users/login',
    register: '/users/register',
    emailAuth: '/users/email',
  },
};

export default url;



